import React from 'react';
import './style.scss';
import logo from '../../assets/img/sample/sampleEvent.png';
import calendar from '../../assets/icon/ico_calendar.png';
//import { sampleData } from '../../sampleData/sampleEvents';

class Page3 extends React.Component {
    constructor(){
        super();
        this.state = {
            displayBio: false,
        };  
        this.toggleDisplayBio = this.toggleDisplayBio.bind(this);  
    }

    toggleDisplayBio(){  
        this.setState({displayBio: !this.state.displayBio});
    } 
    
    render() {
        return (
            <div className='event-details'>
                <div className='event-details-thumbnail'><img src={logo} /></div>
                <div className='event-details-wrapper'>
                    <div className='event-details-title'>New Platinum Conference (NPC)</div>
                    {this.state.displayBio ? (<div className='event-details-desc'>New Platinum Conference (NPC) celebrates the incredible milestone of reaching Platinum, while inspiring Amway Independent Business Owners to continue learning, growing and reaching new heights.
                    <br/><a className='event-details-link'onClick={this.toggleDisplayBio}> View Less </a></div>
                    ):(
                    <a className='event-details-link' onClick={this.toggleDisplayBio}> Read More </a> )}
                </div><br/>
                <div className='event-details-cards'>
                    <div className='item'>
                        <div className='title'>Event Details</div>
                        <div className='icon'><img src={calendar}/></div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Page3;